import React from 'react'
import styled from 'styled-components'
import { Text, Toggle } from 'archerswap-uikit'
import { useExpertModeManager } from 'state/user/hooks'
import { useTranslation } from 'contexts/Localization'
import QuestionHelper from '../QuestionHelper'

const Wrapper = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
`

const Label = styled.div`
  align-items: center;
  display: flex;
  margin-bottom: 8px;
`

const ExpertModeSetting = () => {
  const { t } = useTranslation()
  const [isExpertMode, toggleSetExpertMode] = useExpertModeManager()

  return (
    <Wrapper>
      <Label>
        <Text style={{ fontWeight: 600 }}>{t('Expert Mode')}</Text>
        <QuestionHelper
          text={t('Bypasses confirmation modals and allows high slippage trades. Use at your own risk.')}
        />
      </Label>
      <Toggle checked={isExpertMode} onChange={() => toggleSetExpertMode()} scale="sm" />
    </Wrapper>
  )
}

export default ExpertModeSetting
